import React, { useState } from 'react';
import { sendNotifications } from './services/api';

const App = () => {
  const [title, setTitle] = useState('');
  const [message, setMessage] = useState('');
  const [interval, setInterval] = useState(5);
  const [repetitions, setRepetitions] = useState(3);
  const [status, setStatus] = useState('');

  const handleSendNotifications = async () => {
    try {
      setStatus('Sending notifications...');
      const response = await sendNotifications(title, message, interval, repetitions);
      setStatus(response.message);
    } catch (error) {
      setStatus('Failed to send notifications.');
    }
  };

  return (
    <div style={{ padding: '20px' }}>
      <h1>Notification Sender</h1>
      <form
        onSubmit={(e) => {
          e.preventDefault();
          handleSendNotifications();
        }}
      >
        <div>
          <label>Title: </label>
          <input
            type="text"
            value={title}
            onChange={(e) => setTitle(e.target.value)}
          />
        </div>
        <div>
          <label>Message: </label>
          <input
            type="text"
            value={message}
            onChange={(e) => setMessage(e.target.value)}
          />
        </div>
        <div>
          <label>Interval (seconds): </label>
          <input
            type="number"
            value={interval}
            onChange={(e) => setInterval(e.target.value)}
            min="1"
          />
        </div>
        <div>
          <label>Repetitions: </label>
          <input
            type="number"
            value={repetitions}
            onChange={(e) => setRepetitions(e.target.value)}
            min="1"
          />
        </div>
        <button type="submit">Send Notifications</button>
      </form>
      {status && <p>{status}</p>}
    </div>
  );
};

export default App;
